.StudentListUI {
    font-family: Quicksand;
    width: 100%;
}

.StudentListUI .StudentListUI-Content {
    width: 80%;
    min-height: 100vh;
    margin: 0 auto;
    background-color: #2474c1;
    overflow: hidden;
}

.StudentListUI .StudentListUI-Content .upper {
    padding: 50px 50px 10px 60px;
    background: #fff;
    border-bottom-left-radius: 50px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    max-height: 131.59px;
}

.StudentListUI .StudentListUI-Content .upper .control {
    display: grid;
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
}

.StudentListUI .StudentListUI-Content .upper .control .searchInputs {
    width: 100%;
    border: 0;
    border-bottom: 1px solid lightgray;
    font-size: 12px;
    padding: 5px 10px;
}

.StudentListUI .StudentListUI-Content .upper .control .searchInputs:focus {
    outline: none;
}

.StudentListUI .StudentListUI-Content .upper .control:last-child {
    text-align: right;
}

.StudentListUI .StudentListUI-Content .upper .control:last-child .icons {
    width: 30px;
    min-height: 30px;
    max-height: auto;
    margin-left: auto;
}

.StudentListUI .StudentListUI-Content .upper .control:last-child .icons i {
    font-size: 20px;
    cursor: pointer;
    padding: 5px;
    border-radius: 50%;
}

.StudentListUI .StudentListUI-Content .upper .control:last-child .icons:hover > i {
    animation: popInOut .3s linear 1 forwards alternate;
}

@keyframes popInOut {
    50% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
        background-color: #2474c1;
        color: #fff;
    }
}

/* width */
.StudentListUI .StudentListUI-Content .studentList::-webkit-scrollbar {
    width: 8px;
  }
  
  /* Track */
.StudentListUI .StudentListUI-Content .studentList::-webkit-scrollbar-track {
    background: #32373D;
}
   
  /* Handle */
.StudentListUI .StudentListUI-Content .studentList::-webkit-scrollbar-thumb {
    background: linear-gradient(#2068ae, #5090cd);
}

/* Handle on hover */
.StudentListUI .StudentListUI-Content .studentList::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(#195187, #164674); 
}

.StudentListUI .StudentListUI-Content .studentList {
    padding: 20px;
    max-height: calc( 100vh - 131.59px );
    overflow-x: hidden;
    overflow-y: auto;
}

.StudentListUI .StudentListUI-Content .studentList h4 {
    text-align: center;
    color: #fff;
}

.StudentListUI .StudentListUI-Content .studentList p,
.StudentListUI .StudentListUI-Content .studentList h6 {
    margin-bottom: 0;
}

.StudentListUI .StudentListUI-Content .studentList .LineBreak {
    width: 100%;
    display: grid;
    grid-template-columns: 20fr 40fr 20fr 20fr;
    grid-column-gap: 20px;
}

.StudentListUI .StudentListUI-Content .studentList .line {
    width: 1px;
    height: 50px;
    background-color: #fff;
    margin: 10px auto 0;
}

.StudentListUI .StudentListUI-Content .studentList .Student {
    width: 100%;
    display: grid;
    grid-template-columns: 20fr 40fr 20fr 20fr;
    grid-column-gap: 20px;
    color: #fff;
    transition: .5s;
    cursor: pointer;
    padding-top: 10px;
    opacity: 0;
    transform: scale(0.9);
    animation: popup .3s linear 1 forwards alternate;
}

@keyframes popup {
    1% {
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}


.StudentListUI .StudentListUI-Content .studentList .Student:hover {
    background-color: rgb(255, 255, 255, .1);
}

.StudentListUI .StudentListUI-Content .studentList .Student .id {
    text-align: center;
}

.StudentListUI .StudentListUI-Content .studentList .Student .id p:first-child {
    font-size: 10px;
}

.StudentListUI .StudentListUI-Content .studentList .Student .id p {
    font-size: 25px;
}

.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(2),
.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(3) {
    font-size: 12px;
}

.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(4) {
    display: flex;
    justify-content: center;
}

.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(4) .icons {
    width: 30px;
    margin: 0 10px;
    min-height: 30px;
    height: fit-content;
    max-height: auto;
    cursor: pointer;
}

.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(4) i {
    font-size: 20px;
    padding: 5px;
    border-radius: 50%;
}

.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(4) .icons:hover > i {
    animation: popInOut2 .3s linear 1 forwards alternate;
}

@keyframes popInOut2 {
    50% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
        background-color: #fff;
        color: #2474c1;
    }
}

.StudentListUI .StudentListUI-Content .studentList .Student div:nth-child(5) {
    display: none;
}

@media screen and (max-width: 768px)
{
    .StudentListUI .StudentListUI-Content {
        width: 95%;
    }
    
    .StudentListUI .StudentListUI-Content .studentList p:not( :nth-child(1), :nth-child(2) ) {
        font-size: 12px !important;
    }
}

@media screen and (max-width: 600px)
{
    .StudentListUI .StudentListUI-Content {
        width: 100%;
    }

    .StudentListUI .StudentListUI-Content .upper .control {
        grid-template-columns: 50fr 50fr;
    }

    .StudentListUI .StudentListUI-Content .upper .control div:nth-child(2) {
        display: none;
    }
    
    .StudentListUI .StudentListUI-Content .studentList h6 {
        font-size: 14px;
    }

    .StudentListUI .StudentListUI-Content .studentList .Student {
        grid-template-columns: 20fr 70fr 10fr;
    }

    .StudentListUI-Content .studentList .Student div:nth-child(3) {
        display: none !important;
    }

    .StudentListUI-Content .studentList .Student div:nth-child(4) {
        display: none !important;
    }

    .StudentListUI-Content .studentList .Student div:nth-child(5) {
        display: block !important;
    }

    .StudentListUI .StudentListUI-Content .studentList .LineBreak {
        grid-template-columns: 20fr 70fr 10fr;
    }

    .StudentListUI .StudentListUI-Content .studentList .line {
        height: 30px;
    }
}